.home {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
   
}

.intro {
    max-width: 540 px;
    text-align: center;
}

.home__img {
    margin-bottom: 1.5rem;
    margin-top: 1rem;
    width: 450px;
    border-radius: 150px;
   
}

.home__education{
    margin-bottom: 10rem;
}

.home__name {
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.25rem;
    margin-top: -1rem;
}


.home__socials {
    display: flex;
   margin-top: 1.5rem;
   margin-left: 42%;
    margin-bottom: 0.5rem;
    column-gap: 2rem;
     margin-top: 0.2rem;
}

.home__social-link {
    color: var(--title-color);
    font-size: 2rem;
    transition: .3;
}

.home__social-link:hover {
    color: hsl(330, 85%, 56%);
}

.scroll__down {
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}

.home__scroll-name {
    font-size: var(--small-font-size);
}

.mouse {
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: 0.75rem;
    border-radius: 1rem;
    position: relative;
    margin-top: 0.5rem;
}

@keyframes ani-mouse {
    0% {
        top: 29%;
    }
    15% {
        top: 50%;
    }
    50% {
        top: 50%;
    }
    100% {
        top: 29%;
    }
}

.wheel {
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: 0.5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
   
}

.btn{
    margin-bottom: 100px;
    margin-top: -0.5rem;
}